import { utils } from '../utils/utils';
import { domUtils } from '../utils/dom-utils';
import type { IFullRegion } from '../interface';

/**
 * 获取元素的 { width , height , 距离文档顶部的 top, left 等信息}
 */
function getRegion(node: HTMLElement): IFullRegion {
  let offset: { left: number; top: number; };
  let w: number;
  let h: number;
  if (!utils.isWindow(node) && !utils.isDocument(node)) { // 非window、Document Node
    offset = utils.offset(node) as { top: number, left: number };
    w = domUtils.outerWidth(node);
    h = domUtils.outerHeight(node);
  } else {
    const win = utils.getWindow(node);
    offset = {
      left: utils.getWindowScrollLeft(win),
      top: utils.getWindowScrollTop(win),
    };
    w = domUtils.viewportWidth(win);
    h = domUtils.viewportHeight(win);
  }
  return { ...offset, width: w, height: h };
}

export default getRegion;
